@font-face {
  font-family: 'NanumSquare';
  font-style: normal;
  font-weight: 500;
  src: url(fonts/NanumSquareR.eot) format('eot'),
  url(fonts/NanumSquareR.woff) format('woff'),
  url(fonts/NanumSquareR.ttf) format('truetype');
}

@font-face {
  font-family: 'NanumSquare';
  font-style: normal;
  font-weight: 700;
  src: url(fonts/NanumSquareB.eot) format('eot'),
  url(fonts/NanumSquareB.woff) format('woff'),
  url(fonts/NanumSquareB.ttf) format('truetype');
}

@font-face {
  font-family: 'NanumSquare';
  font-style: normal;
  font-weight: 900;
  src: url(fonts/NanumSquareEB.eot) format('eot'),
  url(fonts/NanumSquareEB.woff) format('woff'),
  url(fonts/NanumSquareEB.ttf) format('truetype');
}

html,body {
  font-family: 'NanumSquare', sans-serif;
  font-size: 100%;
  letter-spacing: -0.01em;
  line-height: 1.618em;
  width: 100%;
  overflow-x: hidden;
}

input[type="password"] {
  font-family: sans-serif;
}

.k-btn {
  display: inline-block;
  font-size: 18px;
  color: #fff;
  background-color: #673ab7;
  border-radius: 4px;
  text-align: center;
  border: 0;
  height: 50px;
  line-height: 50px;
}
.k-btn.full {
  width: 100%;
}
.k-btn.disabled {
  background-color: #ccc;
}

.k-sm__btn {
  display: inline-block;
  border: 1px solid #333;
  border-radius: 4px;
  padding: 0 10px;
  color: #333;
  font-size: 14px;
  height: 24px;
  line-height: 22px;
}
.k-sm__btn:not(:last-child) {
  margin-right: 4px;
}

.k-section {
  width: 1038px;
  padding: 0 15px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}
.k-section-full {
  width: 100%;
}

.k-main__title {
  font-size: 28px;
}
.k-main__title > strong {
  color: #999;
}
.k-sub__title {
  font-size: 18px;
  margin-bottom: 10px;
  text-align: left;
}
.k-main__subTxt {
  color: #333;
  font-size: 20px;
}
.k-section__title {
  font-size: 20px;
  font-weight: normal;
  margin-bottom: 17px;
}

.k-truncate {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.k-hidden {
  opacity: 0;
  width: 0.1px;
  height: 0.1px;
  overflow: hidden;
  text-indent: -9999px;
  position: absolute;
  top: -9999px;
  left: -9999px;
}
.txt-hidden {
  text-indent: -9999px;
}
.txt-purple {
  color: #673ab7 !important;
}
.txt-red {
  color: #f04a6e !important;
}

.master-filter__wrap .master-filter {
  width: 100%;
}
.filter-2 .master-filter__a {
  width: 50%;
}
.filter-3 .master-filter__a {
  width: 33.333%;
  width: calc(100% / 3);
}
.master-filter__a {
  width: 25%;
  width: calc(100% / 4);
  text-align: center;
  float: left;
  height: 50px;
  line-height: 50px;
  font-size: 18px;
  border: 1px solid #ddd;
  background-color: #f7f7f7;
  border-bottom-color: #333;
}
.master-filter__a.active {
  font-weight: bold;
  color: #673ab7;
  background-color: #fff;
  border: 1px solid #333;
  border-bottom-color: transparent;
}
.master-filter__tab {
  display: none;
}
.master-filter__tab.active {
  display: block;
}

.child-filter__wrap {
  width: 100%;
  text-align: center;
}
.child-filter {
  display: inline-block;
}
.child-filter__a {
  display: block;
  min-width: 168px;
  height: 42px;
  line-height: 40px;
  color: #999;
  font-size: 16px;
  border: 1px solid #999;
  float: left;
}
.child-filter__a.active {
  border-color: #333;
  color: #673ab7;
}
.child-filter__a:first-of-type {
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}
.child-filter__a:last-of-type {
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}

.sub-filter__wrap {
  width: 100%;
  text-align: center;
}
.sub-filter {
  display: inline-block;
}
.sub-filter__a {
  display: block;
  float: left;
  color: #999;
  font-size: 16px;
  padding: 9px 0;
}
.sub-filter__a.active {
  color: #673ab7;
  border-bottom: 2px solid #673ab7;
}
.sub-filter__a:not(:last-of-type) {
  margin-right: 75px;
}

.clearfix::after {
  content: '';
  display: table;
  clear: both;
}

.k-list {
  width: 100%;
  border-bottom: 1px solid #efefef;
  overflow: hidden;
}
.k-list.k-list__hd {
  height: 46px !important;
  border: 1px solid #333;
  border-left: 0;
  border-right: 0;
  padding-top: 0 !important;
  padding-bottom: 0 !important;
}
.k-list.k-list__hd > li {
  line-height: 44px !important;
  height: auto !important;
  color: #666 !important;
  font-size: 16px !important;
  padding-top: 0 !important;
  padding-bottom: 0 !important;
}
.k-list.k-list__body {
  cursor: pointer;
}
.k-list > li {
  font-size: 18px;
  padding-bottom: 22px;
  padding-top: 22px;
  float: left;
}
.k-list > li.list-num {
  width: 85px;
  padding-left: 18px;
}
.k-list > li.list-state {
  width: 121px;
}
.k-list > li.list-category {
  width: 162px;
}
.k-list > li.list-subject {
  width: 535px;
}
.k-list > li.list-date {
  width: 104px;
  color: #999;
  font-size: 16px;
}
.list-state__mark {
  display: inline-block;
  width: 66px;
  height: 26px;
  background-color: #999;
  text-align: center;
  color: #fff;
  font-size: 14px;
  line-height: 26px;
  vertical-align: top;
}
.list-state__mark.active {
  background-color: #673ab7;
}

.k-btns .k-btn {
  width: 200px;
  height: 50px;
}
.cancel-btn.k-btn {
  color: #999;
  border: 1px solid #ccc;
  background-color: #fff;
}
.k-btns .cancel-btn.k-btn {
  margin-right: 7px;
}

.k-alert__box {
  width: 100%;
  padding: 20px;
  background-color: #eef4ff;
  text-align: center;
  font-size: 16px;
}
.k-alert__box > .alert-icon {
  display: inline-block;
  width: 18px;
  height: 18px;
  background-image: url('./images/ico-warning.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  vertical-align: middle;
  margin-right: 8px;
  -webkit-trtransform: translateY(-2px);
  transform: translateY(-2px);
}

.k-header {}
.k-header__banner {
  background-color: #161523;
  text-align: center;
}

/* Navigation Bar */
.k-navbar {
  width: 100%;
  border-bottom: 1px solid #eaeaea;
  height: 30px;
}
.k-navbar ul > li {
  float: left;
  line-height: 30px;
  font-size: 13px;
}
.k-navbar .k-site__nav {
  float: left;
}
.k-navbar .k-site__nav > li {
  margin-right: 16px;
  position: relative;
}
.k-navbar .k-site__nav > li:first-child::after {
  content: '';
  display: block;
  position: absolute;
  top: 8.5px;
  right: -8px;
  background-color: #aeaeae;
  width: 1px;
  height: 12px;
}
.k-navbar .k-main__nav {
  float: right;
  margin-right: 16px;
}
.k-navbar .k-main__nav > li {}
.k-navbar .k-main__nav > li::after {
  content: '';
  display: inline-block;
  width: 2px;
  height: 2px;
  background-color: #b6b6b6;
  margin: 0 8px;
  margin-bottom: 3px;
}
.k-navbar .k-main__nav > li:last-child::after {
  display: none;
}
.k-navbar .k-lang__select {
  float: right;
  font-size: 13px;
  margin-top: 5px;
}

/* Search Box */
.k-search__wrap {
  border-bottom: 1px solid #b5b5b5;
}
.k-search__box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.k-logo {
  margin-top: -10px;
}

.k-search__form {
  position: relative;
  margin-top: -10px;
}

.k-notice__thumb {
  position: absolute;
  left: 0;
  bottom: -80%;
  width: 100%;
}
.k-notice__thumb .k-notice__i {
  display: inline-block;
  width: 54px;
  height: 20px;
  border-radius: 4px;
  background-color: #673ab7;
  font-size: 12px;
  color: #fff;
  line-height: 21px;
  text-align: center;
  vertical-align: top;
}
.k-notice__thumb .k-notice__list {
  width: calc(100% - 60px);
  float: right;
  overflow: hidden;
}
.k-notice__thumb .k-notice__list > li {

}
.k-notice__thumb .k-notice__list > li > a {
  display: block;
  font-size: 14px;
  letter-spacing: normal;
  line-height: 20px;
  max-width: 100%;
}

.k-search__input {
  border: 2px solid #673ab7;
  width: 530px;
  height: 40px;
  padding: 0 46px 0 16px;
}

.k-search__btn {
  display: inline-block;
  width: 40px;
  height: 40px;
  background-image: url('./images/ico-search.png');
  background-size: 18px;
  background-position: right 14px center;
  background-repeat: no-repeat;
  position: absolute;
  top: 0;
  right: 0;
}

/* Side Menu */
.k-dimmer {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0,0,0,0.65);
}
.k-sidemenu__wrap {
  display: none;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
}
.k-sidemenu {
  width: 480px;
  height: 100%;
  background-color: #fff;
  box-shadow: -10px 10px 35px rgba(0,0,0,0.25);
  overflow-y: auto;
  position: absolute;
  top: 0;
  right: -100%;
  transition-duration: 0.5s;
  z-index: 2;
}

.k-sidemenu.active {
  right: 0;
}

.k-sidemenu__form {
  width: 100%;
  background-color: #f7f7f7;
  padding: 75px 60px 30px 60px;
  text-align: center;
}

.sidemenu-input__box {
  position: relative;
}
.k-sidemenu__label {
  position: absolute;
  bottom: 35px;
  left: 10px;
  color: #999;
  font-size: 19.5px;
  transition-duration: 0.3s;
}
.k-sidemenu__input {
  width: 100%;
  border: 0;
  padding: 5px 10px;
  padding-top: 0;
  border-bottom: 1px solid #999;
  background-color: transparent;
  font-size: 24px;
  margin-bottom: 25px;
  outline: 0;
}
.k-sidemenu__label.active {
  bottom: 60px;
  left: 6px;
  color: #673ab7;
  font-size: 16px;
}

.k-sidemenu__btn {
  margin-top: 10px;
  margin-bottom: 10px;
}

.k-check__label {
  float: left;
  height: 24px;
  line-height: 24px;
  font-size: 16px;
  padding-left: 33px;
  background-image: url('./images/btn-id-save.png');
  background-size: 24px;
  background-position: left top;
  background-repeat: no-repeat;
  cursor: pointer;
  margin-bottom: 20px;
}

.k-check:checked + .k-check__label {
  background-position: left bottom;
}

.k-client__nav {
  width: 100%;
  clear: both;
}
.k-client__nav > a {
  color: #666;
}
.k-client__nav > a:first-child {
  margin-right: 30px;
  position: relative;
}
.k-client__nav > a:first-child::after {
  content: '';
  display: block;
  width: 1px;
  height: 12px;
  position: absolute;
  top: 2px;
  right: -15px;
  background-color: #ccc;
}
.k-client__nav > a:hover {
  text-decoration: underline;
}

.k-sidemenu__nav {
  width: 100%;
  padding-top: 40px;
}
.k-sidemenu__nav > a {
  display: block;
  width: 100%;
  padding: 27px 0;
  text-align: center;
  font-size: 22px;
  color: #666;
}
.k-sidemenu__nav > a:hover {
  text-decoration: underline;
}

.k-sidemenu__close {
  display: block;
  width: 32px;
  height: 32px;
  position: absolute;
  top: 28px;
  right: 28px;
  background-image: url("./images/ico-x.png");
  background-size: cover;
  background-repeat: no-repeat;
}

/* Poster Slider */
.k-poster__slide {
  overflow: visible;
  margin-top: 30px;
  padding-bottom: 66px;
}

.k-poster__slide .swiper-slide {
  width: 230px !important;
  transition-duration: 0.3s;
  margin-top: 37.795px;
}

/*.k-poster__slide .swiper-slide.swiper-slide-active {
  -webkit-transform: scale(1.1) translateY(7px);
  transform: scale(1.1) translateY(7px);
  transition-duration: 0.3s;
}*/
.k-poster__wrap {
  position: relative;
  margin-bottom: 12px;
}
.k-top__label {
  width: 52px;
  height: 52px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid #ccc;
  font-size: 14px;
  font-weight: bold;
  position: absolute;
  top: -8px;
  right: -8px;
  white-space: pre-line;
  background-color: #fff;
  border-radius: 50%;
  line-height: normal;
  padding-top: 4px;
}
.k-top__label.day-label {
  color: #673ab7;
  border-color: #673ab7;
}
.k-top__label.hot-label {
  color: #4285f4;
  border-color: #4285f4;
}
.k-top__label.end-label {
  color: #4db6ac;
  border-color: #4db6ac;
}

.k-poster__img {
  width: 100%;
  transition-duration: 0.3s;
}

.poster-txt__wrap {
  width: 100%;
  margin-bottom: 20px;
}
.poster-label__wrap {
  width: 100%;
  margin-bottom: 5px;
  min-height: 25px;
}
.poster-label {
  display: inline-block;
  background-color: #fff;
  padding: 0 5px;
  height: 20px;
  border: 1px solid #673ab7;
  line-height: 18px;
  font-size: 13px;
  font-weight: bold;
  color: #673ab7;
  margin-right: 2px;
}
.poster-title {
  font-size: 19px;
  font-weight: bold;
  line-height: 1.2;
  margin-bottom: 6px;
  min-height: 44px;
}
.poster-date {
  font-size: 13.5px;
  margin-bottom: 5px;
  letter-spacing: -0.03em;
  line-height: normal;
}
.poster-place {
  font-size: 13.5px;
  margin-bottom: 5px;
  line-height: normal;
}

.poster-btn__wrap {
  width: 100%;
}

.poster-btn {
  width: 50%;
  height: 40px;
  display: block;
  float: left;
  font-size: 16px;
  border: 1px solid #333;
  line-height: 38px;
  text-align: center;
}
.poster-btn.right-btn {
  color: #fff;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
}
.poster-btn.detail-btn {
  border-right: 0;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  background-color: #fff;
}
.poster-btn.pre-reserv {
  background-color: #673ab7;
}
.poster-btn.on-reserv {
  background-color: #4285f4;
}
.poster-btn.end-reserv {
  background-color: #4db6ac;
}
.k-poster__arrow {
  width: 88px;
  height: 66px;
  background-size: 88px;
  background-repeat: no-repeat;
  background-position: left top -68px;
  top: 35%;
}
.k-poster__arrow::after {
  display: none;
}
.k-poster__arrow:hover {
  background-position: left top;
}
.k-poster__arrow.swiper-button-next {
  background-image: url('./images/slide-btn-next.png');
}
.k-poster__arrow.swiper-button-prev {
  background-image: url('./images/slide-btn-prev.png');
}

/* Promotion */
.k-promotion__wrap {
  background-color: #f7f7f7;
  padding: 47px 0 60px 0;
}
.k-promotion__list {
  display: flex;
  justify-content: space-between;
}
.k-promotion__list > li {
  width: 323.98px;
  width: calc((100% / 3) - 10px);
  background-color: #fff;
  border: 1px solid #d7dadf;
  padding: 30px 0;
  text-align: center;
}
.k-promotion__mark {
  display: inline-block;
  color: #ccc;
  font-size: 14px;
  font-weight: bold;
  margin-left: 30px;
  background-color: #fff;
  width: 52px;
  height: 22px;
  border: 1px solid #ccc;
  border-radius: 6px;
  line-height: 20px;
  float: left;
}
.k-promotion__title {
  color: #999;
  font-size: 16px;
  font-weight: bold;
  margin-right: 30px;
  float: right;
}
.k-promotion__txt {
  font-size: 20px;
  line-height: 1.27;
  margin-bottom: 13px;
  white-space: pre-line;
}
.k-promotion__link {
  color: #673ab7;
  font-size: 16px;
  padding-right: 17px;
  background-image: url('./images/ico-next-purple.png');
  background-size: 7px;
  background-repeat: no-repeat;
  background-position: right top 3px;
}
.k-promotion__img {
  width: 100%;
  margin-top: 15px;
  margin-bottom: 11px;
}

/* Ad */
.k-ad__wrap {
  background-color: #d7c7b8;
}

/* Video */
.k-video__wrap {
  background-color: #f7f7f7;
  padding: 48px 0 40px 0;
}
.k-video__link {
  display: block;
  position: relative;
}
.k-video__img {
  width: 100%;
  margin-bottom: 11px;
}
.k-video__title {
  font-size: 16px;
  line-height: 1.25;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.k-video__arrow {
  width: 30px;
  height: 40px;
  background-color: rgba(0,0,0,0.4);
  background-size: 11px;
  background-position: center;
  background-repeat: no-repeat;
  opacity: 1;
  top: 35%;
  transition-duration: 0.3s;
}
.k-video__arrow.swiper-button-next {
  background-image: url(./images/ico-next.png);
  right: 0;
}
.k-video__arrow.swiper-button-prev {
  background-image: url(./images/ico-prev.png);
  left: 0;
}
.k-video__arrow::after {
  display: none;
}
.k-video__arrow:hover {
    background-color: rgba(103,58,183,0.9);
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
}
.k-video__arrow.swiper-button-disabled {
  pointer-events: all;
  cursor: pointer;
}

/* Concert Detail */
.k-condetail__top {
  padding: 50px 0 40px 0;
  background-color: #fff;
}
.k-condetail__top .k-poster__wrap {
  width: 210px;
  float: left;
}
.k-condetail__top .poster-txt__wrap {
  width: calc(100% - 230px);
  float: right;
  padding-top: 30px;
}
.k-condetail__top .poster-title {
  font-size: 28px;
  margin-bottom: 16px;
}
.k-condetail__top .poster-date {
  font-size: 15px;
  letter-spacing: normal;
  margin-bottom: 3px;
}
.k-condetail__top .poster-place {
  font-size: 15px;
  letter-spacing: normal;
}

.k-condetail__top .k-reserv__btn {
  width: 200px;
  margin-top: 70px;
}

.k-condetail__top .con-map__btn {
  display: inline-block;
  height: 24px;
  padding: 0 9px 0 23px;
  font-size: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  line-height: 23px;
  margin-left: 7px;
  -webkit-transform: translateY(-1px);
  transform: translateY(-1px);
  background-image: url('./images/ico-pin.png');
  background-repeat: no-repeat;
  background-size: 11px;
  background-position: 8px center;
}

.k-condetail__bottom .con-info__box {
  margin-bottom: 45px;
}
.k-condetail__bottom .con-info__subject {
  font-size: 20px;
  margin-bottom: 10px;
}
.k-condetail__bottom .con-info__txt {
  font-size: 16px;
  white-space: pre-line;
  line-height: 1.618em;
}
.k-condetail__bottom .con-info__list {
  width: 100%;
}
.k-condetail__bottom .con-info__list > li {
  font-size: 16px;
}
.k-condetail__bottom .seat-grade__list {
  margin-top: 10px;
}
.k-condetail__bottom .seat-grade__list > li {
  display: inline-block;
  margin-right: 33px;
}
.k-condetail__bottom .seat-grade__color {
  display: inline-block;
  width: 18px;
  height: 18px;
  vertical-align: middle;
  margin-top: -4px;
  margin-right: 6px;
}
.k-condetail__bottom .seat-grade__name {
  margin-right: 15px;
}
.k-condetail__bottom .seat-grade__price {
  font-style: normal;
}
.k-condetail__bottom .master-filter__tab {
  padding-top: 60px;
}
